import axios from 'axios';
import {
    CLOSE_CHANNELS_SUCCEED,
    DEPOSIT_CHANNELS_SUCCEED,
    POLL_CHANNELS,
    PUT_CHANNELS_SUCCEED,
    TASK_COMPLETE, TASK_FAILED
} from "./types";
import {fromWei, toWei} from "../lib/amounts/weiConversion";
import {getDecimals} from "../lib/tokens/tokensLogic";
import {toast} from "react-toastify";
import {SUCCESS_MSG_TOAST, FAIL_MSG_TOAST, NEW_CHANNEL_OPEN} from "../lib/toast/toastConstants";
import {decrementPendingTask, displayToast} from "./actionUtils";

export const pollChannels = () =>
    (dispatch, getState) =>
        new Promise((resolve, reject) =>
            axios.get("http://localhost:5001/api/v1/channels").then(response => {
                return resolve(dispatch(pollSucceed(response.data, checkChannelsChanged(getState().channelReducer.channels, response.data))));
            })
                .catch(error => {
                    console.log(JSON.stringify(error));
                })
        );

export const putChannel = (partnerAddress, tokenAddress, totalDeposit, settleTimeOut) => {
    return (dispatch, getState) => {
        return new Promise((resolve, reject) =>
            axios.put(
                "http://localhost:5001/api/v1/channels",
                {
                    "partner_address": partnerAddress,
                    "token_address": tokenAddress,
                    "total_deposit": toWei(totalDeposit, getDecimals(tokenAddress, getState().tokenReducer.tokens)),
                    "settle_timeout": settleTimeOut
                }
            ).then(response => {
                console.log(JSON.stringify(response));
                displayToast(response, "balance_update");
                return resolve(dispatch(decrementPendingTask(TASK_COMPLETE)));
            })
                .catch(error => {
                    console.log(JSON.stringify(error));
                    displayToast(error.response, null);
                    return resolve(dispatch(decrementPendingTask(TASK_FAILED)));

                })
        );
    }

}


export const closeChannel = (tokenAddress, partnerAddress) =>
    (dispatch, getState) =>
        new Promise((resolve, reject) =>
            axios.patch(
                "http://localhost:5001/api/v1/channels/" + tokenAddress + '/' + partnerAddress,
                {
                    "state": "closed"
                }
            )
                .then(response => {
                    displayToast(response, "close_channel");
                    return resolve(dispatch(decrementPendingTask(TASK_COMPLETE)));
                })
                .catch(error => {
                    console.log(JSON.stringify(error));
                    displayToast(error.response, null);
                    return resolve(dispatch(decrementPendingTask(TASK_FAILED)));
                })
        );

export const depositChannel = (tokenAddress, partnerAddress, totalDeposit, balance) =>
    (dispatch, getState) =>
        new Promise((resolve, reject) =>
            axios.patch(
                "http://localhost:5001/api/v1/channels/" + tokenAddress + '/' + partnerAddress,
                {
                    "total_deposit": (Number(toWei(totalDeposit, getDecimals(tokenAddress, getState().tokenReducer.tokens))) + Number(balance))
                }
            )
                .then(response => {
                    displayToast(response, "deposit_channel");
                    return resolve(dispatch(decrementPendingTask(TASK_COMPLETE)));
                })
                .catch(error => {
                    console.log(JSON.stringify(error));
                    displayToast(error.response, null);
                    return resolve(dispatch(decrementPendingTask(TASK_FAILED)));
                })
        );

const pollSucceed = (channels, dataChanged) => (
    {
        type: POLL_CHANNELS,
        data: {channels: channels, channelsChanged: dataChanged}
    }
);

const putSucceed = (channels, dataChanged) => (
    {
        type: PUT_CHANNELS_SUCCEED,
        data: {channels: channels, channelsChanged: dataChanged}
    }
);

const closeSucceed = (channel) => (
    {
        type: CLOSE_CHANNELS_SUCCEED,
        data: {
            channel: channel
        }
    }
);

const depositSucceed = (channel) => (
    {
        type: DEPOSIT_CHANNELS_SUCCEED,
        data: {
            channel: channel
        }
    }
);

const checkChannelsChanged = (prevStateChannels, newStateChannels) => {
    let result = false;

    var onlyNewChannels = newStateChannels.filter(comparer(prevStateChannels));


    //let newChannels = getNewChannelInfo(prevStateChannels, newStateChannels);

    if (onlyNewChannels.length > 0) {

        if (onlyNewChannels) {

            onlyNewChannels.forEach(function (data, index) {
                let toastData = {status : 201, data};
                displayToast(toastData,"new_channel");
            });

            result = true;

        }

        //let response = {status : 201, data:{channel_identifier:111, partner_address: '0x3E5B85E29504522DCD923aa503b4C502A64AdB7C', token_address:'0xC93554e9983De013fc21fD9e088B78cbD57eCCE3'}}
        //displayToast(response,"new_channel");
        //toast.success(NEW_CHANNEL_OPEN);

    } else {
        //console.log("NO cambiaron")
        result = false;
    }

    return result;
}

const getNewChannelInfo = (prevStateChannels, newStateChannels) => {
    let newChannels = [];

    newStateChannels.forEach(function (newChannel, indexI) {

        if (prevStateChannels.length === 0) {
            newChannels.push(newChannel);
        }

        prevStateChannels.forEach(function (channel, indexJ) {
            if (newStateChannels[indexI].channel_identifier === prevStateChannels[indexJ].channel_identifier) {
                newChannels.push(newStateChannels[indexI]);
            }
        });
    });

    return newChannels;

}

function comparer(otherArray){
    return function(current){
        return otherArray.filter(function(other){
            return other.channel_identifier == current.channel_identifier
        }).length == 0;
    }
}